/* KONTAKT */
.contact{

	@include flex;
	-webkit-box-orient: horizontal;
	-webkit-box-direction: normal;
	-ms-flex-flow: row wrap;
	flex-flow: row wrap;
	-webkit-box-pack: justify;
	-ms-flex-pack: justify;
	justify-content: space-between;
	& form{
		
		-webkit-animation: slideInLeft 1s;
		
		animation: slideInLeft 1s;
		background-color: $main-color;
		-webkit-box-shadow: 5px 10px 22px #0d0d0d;
		box-shadow: 5px 10px 22px #0d0d0d;
		color: $black-color;
		min-width: 40vw;
		margin: 5rem 1rem 5rem 5rem;
		font-weight: bold;
		float: left; // fallback för IE 7 8 9

	}
	& h1{
		
		font-size: 2rem;	
		text-align: center;
		text-decoration: underline;
		letter-spacing: 1px;
		padding: 0.5rem;
	}
}



.li-forms {

	@include flex;
	-ms-flex-direction: column;
	-webkit-box-orient: vertical;
	-webkit-box-direction: normal;
	flex-direction: column;
	margin: auto;
	padding: 0.7rem;
	& button {
		
		-webkit-box-shadow: 0 4px #666;
		
		box-shadow: 0 4px #666;
		background-color: $white-color;
		border: 0;
		border-radius: 20px;
		font-weight: bold;
		width: 50%;
		margin: auto;

		&:hover{

			cursor: pointer;
		}

		&:active {

			-webkit-box-shadow: 0 3px #666;

			box-shadow: 0 3px #666;
			-webkit-transform: translateY(2px);
			-ms-transform: translateY(2px);
			transform: translateY(2px);
		}
	}

	& input, button, label {
		padding: 0.6rem;

	}
		& input:focus, textarea:focus{

			outline: 2px solid $black-color;
		}


}

.contact-wrapper{

	margin: 2rem;
	display: inline-block; // fallback för IE 7 8 9
	float: right // fallback för IE 7 8 9
}


#map{
	
	-webkit-animation: slideInRight 1s;
	
	animation: slideInRight 1s;
	-webkit-box-shadow: 5px 10px 22px #0d0d0d;
	
	box-shadow: 5px 10px 22px #0d0d0d;
	height: 20rem;
	width: 40vw;
	margin: 5rem auto 5rem auto;
	padding: 2rem;
}


.contact-adress{
	

	-webkit-animation: slideInUp 1s;
	

	animation: slideInUp 1s;
	background-color: $main-color;
	-webkit-box-shadow: 5px 10px 22px #0d0d0d;
	box-shadow: 5px 10px 22px #0d0d0d;
	font-weight: bold;
	width: 70%;
	margin: auto;
	& li{

		padding: 1rem;
		margin: 1rem;
	}
}



