/* HOMEPAGE */

main{

	background: url(../img/startbild.jpg) no-repeat center center;
	background-size: cover;
	@include flex;
	-webkit-box-orient: horizontal;
	-webkit-box-direction: normal;
	    -ms-flex-flow: row wrap;
	        flex-flow: row wrap;

}

.slogan{
	
	width: 100%;
	padding-top: 10%; // fallback för IE 7 8 9

& h1{
	
	margin: auto;
	width: 50%;
	text-align: center;
	font-size: 4rem;
	color: $black-color;
	border: 10px solid $black-color;
	font-family: 'Archivo Black', sans-serif;
	-webkit-animation: jackInTheBox 2s;
	        animation: jackInTheBox 2s;
}

}
