*{

	-webkit-box-sizing: border-box;

	        box-sizing: border-box;
	list-style: none;
	text-decoration: none;

}

body,html{
	
	-webkit-animation: fadein 1.5s;
	        animation: fadein 1.5s;
	background: -webkit-linear-gradient(-45deg, rgba(246,248,249,1) 35%,rgba(215,222,227,1) 65%,rgba(215,222,227,1) 70%,rgba(215,222,227,1) 70%,rgba(215,222,227,1) 75%,rgba(215,222,227,1) 77%,rgba(215,222,227,1) 79%,rgba(215,222,227,1) 90%,rgba(215,222,227,1) 95%,rgba(215,222,227,1) 99%,rgba(215,222,227,1) 100%,rgba(245,247,249,1) 100%);
	font-family: 'Open Sans', sans-serif;
	line-height: 1.5;
	margin: auto;

}

ul{

	margin: 0;
	padding: 0;
}

main,.uppdrag,.omoss,.tjänster,.kontakt{

	min-height: 100vh;
}

.hide{

	display: none;
}
